class Day {
  constructor (Classwork, Research, Exercise, Reading, Planning, Reflection, Learning, Meditation, Gratitude, Media, Social, Sleep, Date) {
    this.classwork = Classwork
    this.research = Research
    this.exercise = Exercise
    this.reading = Reading
    this.planning = Planning
    this.reflection = Reflection
    this.learning = Learning
    this.meditation = Meditation
    this.gratitude = Gratitude
    this.media = Media
    this.social = Social
    this.sleep = Sleep
    this.date = Date
  }
}

export default Day
